.wrapper{
    width: 1400px;
    
    margin: 0 auto;
    background-color: $background_color;
}
.container__head__nav{
    display: flex;
    justify-content: space-between;
    padding: 40px;
    margin: 0 30px;
    .head__nav__ul{
        display: flex;
        color: white;
        list-style: none;
        gap:30px;
        font-family: 'Raleway', sans-serif;
       
    }
    
}

.nav__ul__li{
    height: 20px;
    cursor: pointer;
    &:hover{
        border-bottom: 1px solid white;
    }
    
}

.head__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .head__content__picture{
        margin-top: 40px;
    }
    .head__content__h1{
        color: white;
        margin-top: 40px;
        text-align: center;
    }
    .head__content__p{
        color: white;
        width: 500px;
        margin-top: 40px;
        font-family: 'Raleway', sans-serif;
        line-height: 20px;
    }
}

.buttons{
    @include buttons_adjust();
}


